html,
body {
  margin: 0;
  font-family: Helvetica,Arial,’lucida grande’,tahoma,verdana,arial,”hiragino kaku gothic pro”,meiryo,”ms pgothic”,sans-serif;
}

h1, h2, h3, h4, h5 {
  padding: 0.1em;
  margin: 0;
}

.container {
  display: block;
  position: relative;
}

.appbar {
  display: flex;
  justify-content: space-between;
  padding: 1em 1.5em;
  color: #fff;
  background: #02abee;
  box-shadow: 0 2px 5px rgba(42, 42, 42, .42);

  > h1 {
    font-size: 1.15rem;
    margin: 0 .5em;
  }
}

.input-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 40%;
  margin: 5% auto 0;
}

.input-area {
  display: block;
  position: relative;
  width: 100%;
  height: 100px;
  margin: 1em 0;

  label {
    position: absolute;
    top: 15px;
    left: 10px;
    font-size: 16px;
    color: #a0a0a0;
    transition: all 0.25s ease;

    &.focus{
      top: -25px;
      left: 5px;
      font-size: 14px;
      color: #fff;
    }
  }

  input {
    display: block;
    position: relative;
    width: 100%;
    margin-top: 50px;
    padding: 15px;
    border: 2px solid #999;
    border-radius: 5px;
    font-size: 16px;
    color: #666;
    outline: none;
  }
}
